import React from 'react';
import PropTypes from 'prop-types';
import logo from '../../../express/public/static/img/login/logo.png';

console.log('222');
export default class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '1',
            password: '1',
            loading: false
        }
        this.handleUserPasswordChange = this.handleUserPasswordChange.bind(this);
        this.handleUserNameChange = this.handleUserNameChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleUserNameChange(e){
        this.setState({
           username: e.target.value
        });
    }

    handleUserPasswordChange(e){
        this.setState({
            password: e.target.value
        });
    }

    handleSubmit(e){
        this.props.loginRequest();
        this.setState({
            loading: true
        });
        console.log('submit login form');

        if(this.state.username && this.state.password){
         //   this.props.login(this.state.username, this.state.password);
        }
    }

/*    login(){
        console.log('login fired');
        return axios.post(api.auth, {username: api.username, password: api.password} ).then(
            (response) => {
                console.log(response);
        })
            .catch( (error) => {})
    }*/

    render() {
        return (
            <div className="login-bg">
                <div className="container-fluid">
                    <div className="row text-center">
                       <img className="logo" src={logo} alt="Logo" />
                        <br/>
                        <div className="login-wrapper">
                            <div className="input-group">
                                <span className="input-group-addon">
                                     <i className="fa fa-user"></i>
                                </span>
                                <input type="text" className="form-control custom-input" placeholder="Username" value={this.state.username} onChange={this.handleUserNameChange}/>
                            </div>
                            <br/>
                            <div className="input-group">
                                <span className="input-group-addon">
                                     <i className="fa fa-lock"></i>
                                </span>
                                <input type="password" className="form-control custom-input" value={this.state.password} onChange={this.handleUserPasswordChange} placeholder="Password"/>
                            </div>
                            <br/>
                            <button type="button" className="btn login-btn" onClick={this.handleSubmit}>Log In</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

LoginPage.propTypes = {
    loginRequest: PropTypes.func
};