import {LOGIN_REQUEST, LOGIN_SUCCESS, ERROR, LOGIN_FAILURE, LOGOUT} from '../../Constants/Login/index';

let token = JSON.parse(localStorage.getItem('auth_token'));
const initialState = token ? {loggedIn: true, token: token} : {};

export default function login(state ={}, action) {
    console.log('REDUCER STARTED');
    console.log(action.type);
    switch (action.type){
        case LOGIN_REQUEST:
            console.log('login reducer got login request');
            return {loggingIn: true, token: action.token};
        case LOGIN_SUCCESS:
            return {loggedIn: true, token: action.token};
        case LOGIN_FAILURE:
            return {};
        case LOGOUT:
            return {};
        default:
            console.log('login reducer default');
            return state;
    }
}